.profile {

  &__conteiner{
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin-top: 15px;
    position: relative;


    @media (min-width: $screen-sm) {
      display: flex;
      flex-wrap: wrap;

    }

    @media (min-width: $screen-lg) {


  }
  }

  &__title {
    min-height: 1px;
    display: inline-block;

    font-weight: 700;
    font-size: 20px;
    color: #fff;
    position: absolute;
    top: 160px;

    padding-left: 15px;
    // z-index: 100;
    width: 290px;

    @media (min-width: $screen-sm) {
      width: 100%;
      text-align: left;
      display: inline-block;
      position: static;
      font-size: 24px;
      color: #000;
      margin-top: 0;
      margin-bottom: 0;

    }
    @media (min-width: $screen-lg) {
      font-size: 40px;
    }
  }

  &__avatar {

    position: relative;
    height: 290px;
    width: 290px;

    @media (min-width: $screen-sm) {

      display: inline-block;
      height: 215px;
      width: 215px;

    }
    &:after {
      content: '';
      width: 290px;
      height: 290px;
      background-color: #000;
      opacity: 0.3;
      position: absolute;
      top: 0;
      left: 0;

     @media (min-width: $screen-sm) {
      display: none;

    }
  }
  }

  &__avatar-img{
    width: 100%;

  }

  &__list {

  }

  &__action {
  }

  &__subtitle {
    width: 100%;

   font-size: 14px;
   font-weight: 700;

    @media (min-width: $screen-sm) {
      margin-bottom: 0;
      font-size: 20px;

    }

    @media (min-width: $screen-lg) {


    }
  }

  &__inner{
    width: 70%;
      display: flex;
      padding-top: 10px;
    @media (min-width: $screen-sm) {
      padding-top: 0;
       flex-wrap: wrap;
       justify-content: space-between;



    }
  }
  &__wrap{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media (min-width: $screen-sm) {
      flex-wrap: nowrap;
    }

  }
  &__content{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    padding-top: 10px;
    justify-content: space-between;
    @media (min-width: $screen-sm) {
      padding-top: 0;
    }
    @media (min-width: $screen-lg) {
      margin-bottom: 40px;
      margin-top: 0;

    }
  }
  &__text {
    font-family: PT Sans, sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    margin-bottom: 20px;

    @media (min-width: $screen-sm) {
      margin-bottom: 0;
      font-size: 15px;
      line-height: 22px;
      max-width: 410px;
      text-align: left;
      color: #000;
    }

    @media (min-width: $screen-lg) {

    }
  }

  &__video{
    min-height: 234px;
    background-color: lightgrey;
    width: 290px;
    @media (min-width: $screen-lg) {
       width: 500px;
    }

  }

}


.list {
  display: inline-block;

  @media (min-width: $screen-sm) {
    width: 100%;
    display: flex;
    justify-content: space-around;

    }

  &__item {

    font-size: 14px;
    font-weight: 700;
    display: flex;
     @media (min-width: $screen-sm) {
       flex-direction: column;
       }


  }

  &__item-title{
    display: inline-block;
    @media (min-width: $screen-sm) {
      margin-bottom: 15px;
      font-size: 20px;
    }

    @media (min-width: $screen-lg) {

    }


  }

  &__item-text{

    font-size: 14px;
    display: inline-block;
    padding-left: 15px;
    text-align: left;
    text-decoration: none;
    color: $green;
    @media (min-width: $screen-sm) {
      display: block;
      padding-left: 0;
      font-size: 15px;
       color: #000;
    }
    @media (min-width: $screen-lg) {
      font-size: 20px;
    }
  }

  &__item-text-none{
    @media (min-width: $screen-sm) {
      display: none;
    }

  }
  &__item-inner{
    display: inline-block;

  }
  &__item_ski {
    display: none;
    @media (min-width: $screen-sm) {
      display: flex;
      align-self: center;

      width: 31px;
      height: 71px;
      background: url('../images/css_sprites.png') -269px -10px;
    }
  }

}
.action {
  display: inline-block;


  @media (min-width: $screen-sm) {
    display: flex;
    flex-direction: column;
    align-items: flex-end;


  }

  &__btn-score{
      font-size: 20px;
      font-weight: 700;
      text-decoration: none;
      color: #000;
    }

  &__link_flash {
    width: 15px;
    height: 26px;
    background: url('../images/css_sprites-mob.png') -10px -155px;
    margin-right: 5px;


    @media (min-width: $screen-lg) {
      width: 15px;
      height: 26px;
      background: url('../images/css_sprites.png') -78px -238px;
    }
  }

  &__btn {

    font-size: 15px;
    font-weight: 700;
    text-decoration: none;
    color: #fff;
    line-height: 20px;
    padding: 12px 10px;
    text-transform: uppercase;
    border: 2px solid #fff !important;
    // z-index: 102;
    position: absolute;
    top: 213px;
    left: 50%;
    transform: translateX(-50%);

    @media (min-width: $screen-sm) {
      top: auto;
      left: auto;
      margin-top: 0;
      margin-bottom: 10px;
      transform: translateX(0);
      position: static;
      border: 2px solid #000 !important;
      color: $green;
    }

    @media (min-width: $screen-lg) {}

    &--like{
      // display: flex;

      display: inline-block;
      margin-top: 10px;
      padding: 8px 15px;
      text-transform: uppercase;
      border: 2px solid #000;
      text-decoration: none;
      @media (min-width: $screen-sm) {
        margin-top: 0;

      }
    }

}
}
.btn {

}
