.program {
  &__container {
    padding-left: 15px;
    padding-right: 15px;
    @media (min-width: $screen-sm) {
    text-align: center;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;

    }
    @media (min-width: $screen-lg) {
      padding-left: 0;
      padding-right: 0;
      width: 1160px;
    }


  }

  &__title {
    margin-top: 55px;
    font-weight: 700;
    font-size: 24px;
    text-align: left;
    @media (min-width: $screen-sm) {
      margin-bottom: 30px;
      text-align: center;
    }
    @media (min-width: $screen-lg) {
     font-size: 40px;
     margin-bottom: 40px;
    }
  }

  &__inner {

    margin: 0 auto;
  }

  &__table {
    display: none;
  @media (min-width: $screen-sm) {
    display: block;
    text-align: center;
    // display: flex;
    // align-self: center;



    }
  @media (min-width: $screen-lg) {

  }
  }

  &__table-container {
    @media (min-width: $screen-sm) {
    display: flex;
    // align-self: center;
    }
   }

  &__form-schedule {
    display: block;
  @media (min-width: $screen-sm) {
    display: none;
    }
  @media (min-width: $screen-lg) {
  }
  }

  &__btn-wrap{
   text-align: center;
  @media (min-width: $screen-sm) {
  display: none;
    }
  @media (min-width: $screen-lg) {

  }

  }
  &__btn{
    display: inline-block;
    margin-top: 10px;


  @media (min-width: $screen-sm) {
  display: none;
    }
  @media (min-width: $screen-lg) {

  }

  }
}

.btn{
  font-size: 15px;
  font-weight: 700;
  text-decoration: none;
  color: #000;
  line-height: 20px;
  padding: 12px 10px;
  text-transform: uppercase;
  border: 2px solid $green;
}

.form-schedule {
  display: block;
  @media (min-width: $screen-sm) {
  display: none;
    }
  @media (min-width: $screen-lg) {

  }

  &__wrap {
    margin-top: 5px;
    margin-bottom: 12px;


  }
  &__label {
    font-family: PT Sans, sans-serif;
    font-size: 14px;
    font-weight: 700;


  }
  &__item {
    font-size: 12px;
    padding-left: 10px;
    margin-top: 5px;
    width: 100%;
    height: 30px;
    border: 2px solid #000;
  }
}
.table {
  display: none;
  @media (min-width: $screen-sm) {
    display: block;
    border-collapse: collapse;
    margin: 0;
    padding: 0;
    width: 100%;
    align-self: center;




    }
  @media (min-width: $screen-lg) {

  }

  &__row {
    text-align: left;
    border: 2px solid #000;
  }
  &__row-title {
    text-align: left;
    border: none;
  }

  &__cell {
    @media (min-width: $screen-sm) {
      font-size: 14px;
      font-weight: 500;
      padding: 10px;
      padding-left: 15px;
      padding-right: 15px;

      text-align: left;
      border: 2px solid #000;
    }
    @media (min-width: $screen-lg) {
      font-size: 15px;
    }
  }

  &__cell-title {
    @media (min-width: $screen-sm) {
      font-size: 15px;
      font-weight: 700;
      padding: 10px;
      padding-left: 0;
      padding-right: 15px;
      margin: 0;
      text-align: left;

    }
    @media (min-width: $screen-lg) {
      font-size: 15px;

    }

  }


}
.link {
  font-size: 15px;
  font-weight: 700;
  text-decoration: none;
  color: $green;
  text-transform: uppercase;

}
