.feedback {
    &__container {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    text-align: left;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 20px;
    @media (min-width: $screen-sm) {
      padding-left: 0;
      padding-right: 0;
      margin-bottom: 32px;
  }
  @media (min-width: $screen-lg) {
    margin-bottom: 60px;
  }

  &__title {
    margin-top: 55px;
    font-weight: 700;
    font-size: 24px;
    text-align: center;

    @media (min-width: $screen-sm) {
      margin-top: 10px;
      margin-bottom: 30px;
    }
    @media (min-width: $screen-lg) {

      font-size: 40px !important;
      margin-bottom: 40px;
    }
  }

    &__form {

    }

    &__btn-wrap{
      display: flex;
      @media (min-width: $screen-lg) {
       margin-top: 60px;
      }

    }
    &__btn {
     align-self: center;
    }
  }
}
.form {
  margin: 0 auto;
  width: 290px;
  display: flex;
  flex-direction: column;
  align-items: stretch;

  @media (min-width: $screen-sm) {

  }

  @media (min-width: $screen-lg) {
    width: 556px;
  }

  &__label {
    align-self: flex-start;
    display: block;
    font-family: PT Sans, sans-serif;
    font-size: 12px;
    font-weight: 700;
    margin-bottom: 5px;
    @media (min-width: $screen-lg) {
     margin-bottom: 10px;
     font-family: Futura PT;
     font-size: 20px;
     font-weight: 500;
    }
  }
  &__input {
    display: block;
    font-size: 12px;
    padding-left: 10px;
    margin-bottom: 5px;
    height: 30px;
    border: 2px solid #000;
    @media (min-width: $screen-sm) {

    }

    @media (min-width: $screen-lg) {
      height: 45px;
      margin-bottom: 20px;
      font-size: 15px;

    }
  }
  &__textarea {
    resize: vertical;
    font-size: 12px;
    padding-top: 5px;
    padding-left: 10px;
    margin-bottom: 5px;
    height: 96px;
    border: 2px solid #000;
    @media (min-width: $screen-lg) {
      font-size: 15px;

    }

  }
  &__btn{
    align-self: center;
    margin-top: 30px !important;
    background-color: #fff;
  }
}
.btn {

  font-size: 15px;
  font-weight: 700;
  text-decoration: none;
  color: $green;
  line-height: 20px;
  padding: 12px 10px;
  text-transform: uppercase;
  border: 2px solid #000;
}
